.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.modal-egreement__wrap, .modal-form__notfication, .booking-form__notfication, .benefits__item, .close-btn, .social-icons__link, .social-icons__list {
  justify-content: center;
  align-items: center;
  display: flex;
}

.menu__socials-link--decorated, .contacts__link, .socials__list, .section-uppertitle, .site-nav__list {
  align-items: center;
  display: flex;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 690px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.logo {
  fill: currentColor;
  transition-property: color;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
}

.logo--black {
  color: var(--logo-black-cl);
}

.logo--white {
  color: var(--logo-white-cl);
}

.site-nav {
  display: flex;
}

.site-nav__list {
  gap: var(--indent);
  margin-left: 70px;
}

@media screen and (min-width: 768px) {
  .site-nav__list {
    font-size: var(--fs-nav);
    line-height: var(--lh-nav);
    letter-spacing: var(--ls-nav);
    gap: 20px;
    margin-left: 49px;
  }
}

@media screen and (min-width: 1200px) {
  .site-nav__list {
    margin-left: 131px;
  }
}

.site-nav__link {
  transition-property: color;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
}

.site-nav__link:hover, ._site-nav--current {
  color: var(--accent-cl);
}

.btn {
  font-weight: var(--fw-l);
  font-size: var(--fs-btn);
  line-height: var(--lh-btn);
  letter-spacing: var(--ls-btn);
  text-transform: uppercase;
  border: 1px solid var(--accent-cl);
  border-radius: var(--btn-border-radius);
  min-width: 160px;
  min-height: 40px;
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  padding: 0;
  display: inline-flex;
}

.btn--white {
  color: var(--title-txt-cl);
  transition-property: color, background-color, box-shadow;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
}

.btn--white:hover, .btn--white:focus {
  color: var(--white-txt-cl);
  background-color: var(--accent-cl);
  box-shadow: var(--btn-shadow);
}

.btn--dark {
  max-width: 160px;
  color: var(--white-txt-cl);
  transition-property: background-color, box-shadow;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
  margin: 0 auto;
  display: flex;
}

.btn--dark:hover, .btn--dark:focus {
  background-color: var(--accent-cl);
  box-shadow: var(--btn-shadow);
}

.slick-slider {
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
  display: block;
  position: relative;
}

.slick-list {
  margin: 0;
  padding: 0;
  display: block;
  position: relative;
  overflow: hidden;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track, .slick-slider .slick-list {
  transform: translate3d(0, 0, 0);
}

.slick-track {
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: relative;
  top: 0;
  left: 0;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  height: auto;
  border: 1px solid rgba(0, 0, 0, 0);
  display: block;
}

.slick-arrow.slick-hidden {
  display: none;
}

.slick-slider {
  position: relative;
}

@media screen and (max-width: 767px) {
  .slick-slider {
    margin: -15px;
  }
}

@media screen and (min-width: 480px) {
  .slick-slider {
    max-width: 480px;
    height: 580px;
  }
}

@media screen and (min-width: 768px) {
  .slick-slider {
    max-width: 573px;
    height: 680px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .slick-slider {
    margin-left: -15px;
  }
}

@media screen and (min-width: 1200px) {
  .slick-slider {
    max-width: 1070px;
  }
}

@media screen and (min-width: 480px) {
  .slider-item {
    max-width: 480px;
    max-height: 580px;
  }
}

@media screen and (min-width: 768px) {
  .slider-item {
    max-width: 573px;
    max-height: 680px;
  }
}

@media screen and (min-width: 1200px) {
  .slider-item {
    max-width: 1070px;
    max-height: 680px;
  }
}

.slick-arrow {
  color: rgba(0, 0, 0, 0);
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  padding: 0;
  font-size: 0;
  position: absolute;
  bottom: 40px;
  right: 20px;
}

.slick-prev {
  z-index: 1;
  right: 104px;
  transform: translateY(-100%);
}

.slick-prev:before {
  content: "Back";
  letter-spacing: .1em;
  color: var(--slider-btn-cl);
  border-bottom: 1px solid var(--slider-btn-cl);
  transition-property: color, border-bottom;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
  padding-bottom: 8px;
  padding-right: 31px;
  font-size: 11px;
  font-weight: 500;
  line-height: 1.36;
}

.slick-prev:hover:before {
  color: var(--slider-btn-white-cl);
  border-bottom: 1px solid var(--slider-btn-white-cl);
}

.slick-next {
  transform: translateY(-100%);
}

.slick-next:before {
  content: "Forward";
  letter-spacing: .1em;
  color: var(--slider-btn-cl);
  border-bottom: 1px solid var(--slider-btn-cl);
  transition-property: color, border-bottom;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
  padding-bottom: 8px;
  padding-left: 9px;
  font-size: 11px;
  font-weight: 500;
  line-height: 1.36;
}

.slick-next:hover:before {
  color: var(--slider-btn-white-cl);
  border-bottom: 1px solid var(--slider-btn-white-cl);
}

.slick-dots {
  text-align: right;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.slick-dots button {
  width: 8px;
  height: 2px;
  background-color: var(--slider-pagination-cl);
  border: none;
  padding: 0;
  font-size: 0;
}

.slick-active button {
  width: 18px;
  height: 2px;
  background-color: #fff;
}

.card-set {
  gap: var(--indent);
  flex-wrap: wrap;
  display: flex;
}

.card-item {
  flex-basis: calc((100% - var(--indent) * (var(--item) )  - 1) / var(--item) );
}

.section-uppertitle {
  font-weight: var(--fw-l);
  font-size: var(--fs-uppertitle-s);
  line-height: var(--lh-uppertitle-s);
  text-transform: uppercase;
  color: var(--title-txt-cl);
  gap: 20px;
  margin-bottom: 20px;
}

@media screen and (min-width: 480px) {
  .section-uppertitle {
    font-size: var(--fs-uppertitle-m);
    line-height: var(--lh-uppertitle-m);
  }
}

.section-uppertitle:before {
  content: "";
  width: 60px;
  height: 1px;
  background-color: currentColor;
}

.section-title {
  font-family: var(--title-font);
  font-size: var(--fs-section-title-s);
  line-height: var(--lh-section-title-s);
  letter-spacing: var(--ls-section-title);
  color: var(--title-txt-cl);
}

@media screen and (min-width: 480px) {
  .section-title {
    font-size: var(--fs-section-title-l);
    line-height: var(--lh-section-title-m);
  }
}

.social-icons__list {
  gap: 10px;
}

.social-icons__link {
  background-color: var(--background-white-cl);
  fill: currentColor;
  border-radius: var(--socials-icon-border-radius);
  width: 44px;
  height: 44px;
  transition-property: fill;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
}

.social-icons__link:hover, .social-icons__link:focus {
  fill: var(--accent-cl);
}

.socials__list {
  font-weight: var(--fw-m);
  font-size: var(--fs-socials);
  line-height: var(--lh-socials);
  letter-spacing: var(--ls-socials);
  justify-content: right;
  gap: 40px;
}

@media screen and (max-width: 767px) {
  .socials__list {
    margin-bottom: 20px;
  }
}

.socials__list:before {
  content: "";
  width: 60px;
  height: 1px;
  background-color: var(--title-txt-cl);
}

.socials__link {
  color: var(--title-txt-cl);
  transition-property: color;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
}

.socials__link:hover, .socials__link:focus {
  color: var(--accent-cl);
}

.work-time__title {
  color: var(--white-txt-cl);
}

.work-time__title:before {
  background-color: var(--psevdoelement-light-cl);
}

.work-time__descr {
  font-size: var(--fs-work-time);
  line-height: var(--lh-work-time);
  letter-spacing: var(--ls-work-time);
  color: var(--secondary-txt-cl);
}

.close-btn {
  color: var(--close-btn-cl);
  transition-property: color;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  position: absolute;
  top: 15px;
  right: 15px;
}

.close-btn:hover, .close-btn:focus {
  color: var(--accent-cl);
}

.close-btn__icon {
  fill: currentColor;
}

.header-mobile {
  width: 100%;
  z-index: 100;
  padding-top: 20px;
  padding-bottom: 20px;
  position: fixed;
}

@media screen and (min-width: 768px) {
  .header-mobile {
    display: none;
  }
}

.header-mobile--no-transparency {
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  background-color: rgba(25, 28, 38, .5);
}

.header-mobile__container {
  color: var(--icon-white-cl);
  justify-content: space-between;
  display: flex;
}

.header-mobile__burger {
  color: var(--icon-white-cl);
  transition-property: color;
  transition-duration: var(--duration);
  transition-timing-function: var(--timing-function);
  background-color: rgba(0, 0, 0, 0);
  border: none;
